import {Api} from ".";
import config from "../config/config";

import {useCallback, useContext} from "react";

import {deleteStorage, setStorage, getTokenStorage} from "../helpers/localStorage";

import {Context as UsuarioContext} from "../context/UsuarioContext";
import {Context as TokenContext} from "../context/TokenContext";

export const login = (values) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setUsuario} = useContext(UsuarioContext);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setJWT} = useContext(TokenContext);
  const res = Api({
    url: `${config.DOMAIN}proyecto/login`,
    method: "POST",
    data: {
      correo: values.correo,
      contraseña: values.contraseña,
    },
  })
    .then((res) => {
      setStorage(res.token, res.usuario);
      setJWT(res.token);
      setUsuario(res.usuario);
    })
    .catch((err) => err);
  return res;
};

export const registro = async (values) => {
  const res = await Api({
    url: `${config.DOMAIN}proyecto/user`,
    method: "POST",
    data: values,
  })
    .then((res) => res.data)
    .catch((err) => err);
  return res;
};

export const logout = async () => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setUsuario} = useContext(UsuarioContext);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setJWT} = useContext(TokenContext);
  setJWT(null);
  setUsuario(null);
  deleteStorage();
};

export const borrar = async () => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setUsuario} = useContext(UsuarioContext);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setJWT} = useContext(TokenContext);
  const token = getTokenStorage();
  console.log(token);
  Api({
    url: `${config.DOMAIN}proyecto/user`,
    method: "DELETE",
    headers: {token: token},
  })
    .then()
    .catch((err) => {
      setJWT(null);
      setUsuario(null);
      deleteStorage();
    });
};

export const borrarByCorreo = async (correo) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setUsuario} = useContext(UsuarioContext);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {jwt, setJWT} = useContext(TokenContext);
  const token = getTokenStorage();
  console.log(token);
  Api({
    url: `${config.DOMAIN}proyecto/user/${correo}}`,
    method: "DELETE",
    headers: {token: token},
  })
    .then()
    .catch((err) => {
      setJWT(null);
      setUsuario(null);
      deleteStorage();
    });
};

export const comprobarToken = async () => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setUsuario} = useContext(UsuarioContext);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {setJWT} = useContext(TokenContext);
  const token = getTokenStorage();
  console.log(token);
  Api({
    url: `${config.DOMAIN}proyecto/comprobarToken`,
    method: "GET",
    headers: {token: token},
  })
    .then()
    .catch((err) => {
      setJWT(null);
      setUsuario(null);
      deleteStorage();
    });
};
